*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.content{
  height: 150vh;
  margin: 0;
}

.heading h6{
  margin: 0 !important;
  /* height: 2rem; */
}

.register-page{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 90vh;
}

.regis a{
  text-decoration: none;
  font-weight: 700;
}

/* transcation page  */
.filters{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  box-shadow: 0 0 3px gray;
}

/* antd icons  */

.switch-icon{
  border: 1px solid rgba(0,0,0,0.677);
  border-radius: 5px;
  padding: 10px 15px;
}
.anticon svg{
  font-size: 20px;
  cursor: pointer;
}

.anticon-icon{
  color: black;
}

.inactive-icon{
  color: gray;
}